import clsx from "clsx";
import React, { type JSX } from "react";

interface BentoBoxProps {
  children?: React.ReactNode;
  variant?: "primary" | "secondary" | "accent" | "neutral";
  size?: "small" | "medium" | "large";
  className?: string;
  onClick?: () => void;
}

export function BentoBox({
  children,
  variant = "primary",
  size = "small",
  className,
  onClick,
}: BentoBoxProps): JSX.Element {
  const sizeClasses = {
    small: "col-span-1 row-span-1",
    medium: "col-span-2 row-span-1",
    large: "col-span-2 row-span-2",
  };

  const variantClasses = {
    primary: "bg-base-200 text-base-content",
    secondary: "bg-base-200 text-base-content",
    accent: "bg-base-200 text-base-content",
    neutral: "bg-base-200 text-base-content",
  }[variant];

  return (
    <div
      className={clsx(
        "card transition-all duration-300 group",
        "hover:scale-[1.02] hover:shadow-xl",
        "backdrop-blur-sm bg-opacity-90",
        "border border-base-content/10",
        "prose prose-xl",
        "break-inside-avoid",
        sizeClasses[size],
        variantClasses,
        className
      )}
      onClick={onClick}
    >
      <div className="card-body">
        {children}
      </div>
    </div>
  );
}
